<template>
<div id="rentCar">
   <h2>商家如何查看运营数据？</h2>
   <p>商家在工作台数据看板查看门店数据，包括收益数据、天天租车辆数据、分时租车辆数据。</p>
  <p>今日收益=天天租用户提车后订单金额+分时租用户完成订单金额</p>
  <p>今日浏览=用户访问店铺次数</p>
  <p>今日订单金额=今日产生租车订单总金额，包括所有状态的天天租车订单、分时租车订单。</p>
  <p>今日完成订单金额=今日已完成天天租车、分时租车订单。</p>
  <p>出租中：出租中车辆</p>
  <p>待归还：车辆租期今日到期及已逾期未归还的车辆</p>
  <p>未出租：还未出租的车辆；已下架：已下架不能出租的车辆。</p>
</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">
#rentCar{
 padding: 0 40px;
  box-sizing: border-box;
  h2{
    font-size:28px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height: 100px;
    border-bottom: 1px solid #F0F0F0;
    text-align: left;
  }
  p{
    font-size:28px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(72,72,72,1);
    line-height:40px;
    margin-top: 38px;
    text-align: left;
  }
  .imgDiv{
    width:400px;
    margin: 40px auto;
    margin-top: 70px;
    img{
      height: 100%;
      width: 100%;
    }
  }
}
</style>



